.skills {
    background-color: #f9f9f9;
    padding: 60px 20px;
    text-align: center;
  }
  
  .skills .container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .skills h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #333;
  }
  
  .skills-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .skill-item {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    margin: 10px;
    flex: 1 1 250px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .skill-item h3 {
    font-size: 1.5em;
    margin-bottom: 15px;
    color: #007BFF;
  }
  
  .skill-item ul {
    list-style: none;
    padding: 0;
  }
  
  .skill-item ul li {
    font-size: 1.2em;
    margin: 5px 0;
    color: #555;
  }
  