/* About.css */

.about {
    padding: 50px 0;
    background-color: #f4f4f9; /* Light background color for the section */
    color: #333; /* Text color */
    font-family: 'Arial', sans-serif;
  }
  
  .about .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .about h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #2c3e50; /* Dark color for the heading */
    text-align: center;
    position: relative;
    padding-bottom: 10px;
  }
  
  .about h2::after {
    content: "";
    display: block;
    width: 60px;
    height: 3px;
    background: #e74c3c; /* Highlight color */
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .about p {
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .about ul {
    list-style-type: none;
    padding: 0;
  }
  
  .about li {
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    transition: background 0.3s, border-color 0.3s;
  }
  
  .about li:hover {
    background: #eaf4f4;
    border-color: #1abc9c; /* Highlight color */
  }
  
  .about strong {
    color: #2c3e50; /* Dark color for strong text */
    display: block;
    margin-top: 10px;
    margin-bottom: 5px;
  }
  
  .about .highlight {
    background: #1abc9c; /* Highlight background color */
    color: #fff;
    padding: 5px 10px;
    border-radius: 3px;
  }
  
  .about .highlight-dark {
    background: #34495e; /* Darker highlight color */
  }
  
  .about .highlight-light {
    background: #ecf0f1; /* Light highlight color */
  }
  
  .about .achievements, .about .interests {
    margin-top: 30px;
  }
  
  .about .achievements p, .about .interests p {
    margin-bottom: 10px;
  }
  
  .about .achievements ul, .about .interests ul {
    padding-left: 20px;
  }
  
  .about .achievements li, .about .interests li {
    font-size: 1.1rem;
    margin-bottom: 10px;
  }
  
  @media (max-width: 768px) {
    .about h2 {
      font-size: 2rem;
    }
  
    .about li {
      font-size: 0.9rem;
    }
  }
  